/* eslint-disable react/destructuring-assignment */
/* eslint react/prop-types: 0 */

import React, { Component } from 'react';
import { graphql } from 'gatsby';
import { MDXRenderer } from 'gatsby-plugin-mdx'

import { parseChineseDate } from '../api';

import Sidebar from '../components/Sidebar';
import SEO from '../components/SEO';
import Header from '../components/Header';
import TableOfContents from '../components/TableOfContent';
import { config } from '../../data';
import { parseStaticImg } from '../api/images';

// Styles
import './general.scss';

const { name, iconUrl,
  meta,
  license,
 } = config;

const bgWhite = { padding: '10px 30px', background: 'white' };

// Prevent webpack window problem

class General extends Component {
  constructor(props) {
    super(props);
    this.post = this.props.data.mdx
  }

  componentDidMount() {
  }

  render() {
    const {
       frontmatter, excerpt
    } = this.post;

    const {headerImage, title, tags, slug } = frontmatter;
    return (
      <div className="row post order-2">
        <Sidebar />      
        <div className="col-xl-7 col-lg-6 col-md-12 col-sm-12 order-10 content">
          <MDXRenderer>{this.post.body}</MDXRenderer>         
          <div className="m-message" style={bgWhite}>
            <font size="3" color="#542c85">
            {license.copyright}
            </font>
          </div>
        </div>
        <SEO
          title={title}
          url={slug}
          siteTitleAlt={meta.description}
          isPost={false}
          description={excerpt}
          image={headerImage? parseStaticImg(headerImage): "/favicons/default.png"}
        />  
        <TableOfContents
            items={this.post.tableOfContents.items}
            location={this.props.location}
            depth={4}
            tags={tags}
        />          
      </div>
    );
  }
}
    
export default General

export const pageQuery = graphql`
  query {
    mdx(frontmatter: { slug: { eq: "/home/"  } }) {
      id
      tableOfContents
      excerpt(pruneLength: 160)
      frontmatter {
        id
        title
        slug
        date
        headerImage
        tags
      }
      body
    }
  }
`
