import React from 'react';
import { Link, StaticQuery, graphql } from 'gatsby';
import PropTypes from 'prop-types';

import { config } from '../../../data';

// import Information from './Information';

import './index.scss';

const {
  wordings = [],
  githubUrl,
  googleScholarUrl,
  email,
  orcid,
  iconUrl,
  about,
  name,
} = config;

const Icon = ({ href, icon, alt, name }) => (
  <a
    target="_blank"
    href={href}
    rel="external nofollow noopener noreferrer"
    className="custom-icon"
  >
    <span className="fa-layers fa-fw fa-2x">
      <img src={icon} alt={alt} width="32" height="32" />
    </span>
    {name && <span>{name}</span>}
  </a>
);

const Sidebar = ({ totalCount, latestPosts }) => (
  <header className="intro-header site-heading text-center col-xl-2 col-lg-3 col-xs-12 order-lg-1">
    <div className="about-me">
      <Link to={about} href={about} className="name">
        <img className="avatar" src={iconUrl} alt={name} />
        <h4>{name}</h4>
      </Link>
      
      <p class="info">PhD Researcher</p>
      <p class="info">Wuhan University</p>
      <hr />
      <p className="mb-1">{wordings[0]}</p>
      <p className="mb-2">{wordings[1]}</p>
      <p className="mb-3">{wordings[2]}</p>
      {/* <p className="mb-1">{wordings.join(" ")}</p> */}
        <Icon
          href={googleScholarUrl}
          icon="/favicons/google-scholar.svg"
          alt="google-scholar"
        />
        <Icon
          href={orcid}
          icon="/favicons/orcid.svg"
          alt="oricid"
        />
        <Icon 
          href={`mailto:${email}`} 
          icon="/favicons/envelope.svg" 
          alt="mailenvelope"
        />
        <Icon
          href={githubUrl}
          icon="/favicons/github.svg"
          alt="github"
        />

      
      {/* <Information totalCount={totalCount} posts={latestPosts} /> */}

    </div>
  </header>
);

Icon.propTypes = {
  href: PropTypes.string.isRequired,
  icon: PropTypes.string.isRequired, // 使用string而不是array
};

Sidebar.propTypes = {
  totalCount: PropTypes.number,
  latestPosts: PropTypes.array, //eslint-disable-line
};

Sidebar.defaultProps = {
  totalCount: 0,
  latestPosts: [],
};

export default () => (
  <StaticQuery
    query={graphql`
      fragment cardData on Mdx {
        frontmatter {
          id
          title
          url: slug
          date
          tags
          description
          headerImage
        }
      }

      query SidebarQuery {
        all: allMdx {
          totalCount
        }

        limited: allMdx(
          sort: { order: DESC, fields: frontmatter___date }
          limit: 6
        ) {
          latestPosts: edges {
            node {
              ...cardData
            }
          }
        }
      }
    `}
    render={data => <Sidebar {...data.all} {...data.limited} />}
  />
);
